.mobile_menu {
  position: sticky;
  background-color: #595959;
  width: 100vw;
  // height: 100vh;

  ul {
    padding: 1rem;
    text-align: center;

    li {
      margin: 4rem;
      list-style: none;
      font-weight: 500;
      transition: all 0.5s ease;

      &:hover {
        transform: translateY(5px);
      }

      a {
        color: white;
        text-decoration: none;
      }
    }
  }
}

@media screen and (min-width: 765px) {
  .mobile_menu {
    display: none;
  }
}
