.hero {
  // display: flex;
  // flex-direction: column;
  // justify-content: flex-end;
  display: flex;
  justify-content: space-between;
  align-items: space-between;
  margin: 0rem 1.5rem;
  margin-top: 5rem;
  color: white;

  h2 {
    margin-top: 1rem;
    font-size: 1.75rem;
  }

  &__desc {
    margin-top: 1rem;
    line-height: 1.6;
    font-size: 0.95rem;
    color: #bec3c5;
  }

  &__img {
    display: none;

    img {
      width: 25rem;
      margin-left: 5rem;
    }
  }
}

.button_container {
  margin-top: 1rem;
  button {
    border-radius: 0.55rem;
    font-family: inherit;
    font-size: 0.9rem;
    margin: 0.5rem 0rem;
    margin-right: 1rem;
    padding: 0.75rem 2rem;
    width: 270px;
    max-width: 270px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.5s ease;
  }

  &__resume {
    background-color: #7858fe;
    color: white;
    border: none;

    &:hover {
      transform: translateY(5px);
      background-color: #5f43d1;
    }
  }

  &__contact {
    background-color: transparent;
    border: 2px solid #7858fe;
    color: #7858fe;

    &:hover {
      background-color: #7858fe;
      transform: translateY(5px);

      color: #ffffff;
    }
  }
}

@media screen and (min-width: 765px) {
  .hero {
    margin: 8rem 2rem;
    // max-width: 400px;
  }
}

@media screen and (min-width: 1025px) {
  .hero {
    margin: 8rem 4rem;

    &__intro {
      font-size: 1.1rem;
    }

    &__desc {
      font-size: 0.95rem;
    }

    h2 {
      font-size: 3rem;
    }
  }
}

@media screen and (min-width: 1150px) {
  .hero {
    &__img {
      display: block;
    }
  }
}

@media screen and (min-width: 1500px) {
  .hero {
    &__img {
      img {
        width: 35rem;
      }
    }
  }
}

@media screen and (min-width: 1750px) {
  .hero {
    margin: 8rem 20rem;
    max-width: 2000px;

    &__intro {
      font-size: 1.15rem;
    }

    h2 {
      font-size: 3.5rem;
    }

    &__desc {
      font-size: 1rem;
    }
  }
}

.button_container {
  margin-top: 3rem;
}
