.card {
  margin: 3rem 0rem;
  padding: 1.5rem;
  border-radius: 1rem;
  // background: linear-gradient(to left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 100)),
  //   url(../../Assets/Images/movie.jpg);
  h2 {
    font-size: 1.4rem;
    font-weight: 600;
  }

  p {
    margin-top: 2rem;
    line-height: 1.5;
    font-size: 0.9rem;
  }

  &__btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 6rem;
    button {
      border-radius: 0.55rem;
      font-family: inherit;
      font-size: 0.9rem;
      margin: 0.5rem 0rem;
      padding: 0.75rem 2rem;
      width: 165px;
      font-weight: 500;
      cursor: pointer;
      transition: all 0.5s ease;
      display: flex;
      align-items: center;

      svg {
        font-size: 1rem;
        margin-left: 0.5rem;
      }
    }
  }
}

@media screen and (min-width: 765px) {
  .test {
    padding: 1rem 2rem;
  }
}

@media screen and (min-width: 1025px) {
  .test {
    padding: 1rem 4rem;
  }
}

@media screen and (min-width: 1750px) {
  .test {
    padding: 1rem 20rem;
  }
}
