.test {
  margin-top: 5rem;
  padding: 1rem 1rem;
  padding-top: 1rem;
  background-color: #2f2e41;
  color: white;
  text-align: center;
}

@media screen and (min-width: 765px) {
  .test {
    padding: 1rem 2rem;
  }
}

@media screen and (min-width: 1025px) {
  .test {
    padding: 1rem 4rem;
  }
}

@media screen and (min-width: 1750px) {
  .test {
    padding: 1rem 20rem;
  }
}
