.footer {
  margin-top: 2rem;
  margin-bottom: 2rem;
  padding: 0rem 1rem;
  background-color: #1d1e22;
  color: white;
  text-align: center;

  p {
    color: #bec3c5;
    margin-top: 1rem;
    line-height: 1.5;
    font-size: 0.9rem;
  }

  button {
    font-family: inherit;
    border: none;
  }

  &__email_btn {
    border-radius: 0.5rem;
    font-size: 0.9rem;
    font-weight: 600;
    margin: 1.25rem auto;
    margin-top: 2rem;
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;

    svg {
      font-size: 1.25rem;
      margin-left: 1rem;
    }

    cursor: pointer;
  }

  &__socials {
    display: flex;
    align-items: center;
    justify-content: center;

    button {
      padding: 0.5rem 0.6rem;
      margin: 0.65rem;
      border-radius: 4rem;
      transition: all 0.5s ease;
      cursor: pointer;

      &:hover {
        transform: translateY(5px);
      }

      &:last-child {
        &:hover {
          background-color: #0076b4;
          color: white;
        }
      }
    }

    svg {
      font-size: 1.25rem;
    }
  }

  &__copyright {
    margin-top: 6rem;
  }
}
