.header {
  margin: 1.5rem;
  display: flex;
  position: sticky;
  align-items: center;
  justify-content: space-between;

  &__title {
    a {
      font-size: 1.25rem;
      color: rgb(218, 218, 218);
      text-decoration: none;
      transition: all 0.5s ease;

      &:hover {
        color: white;
      }
    }
  }

  // Desktop Nav
  &__desktop_nav {
    display: none;

    ul {
      display: flex;
      align-items: center;

      li {
        margin: 1rem;
        list-style: none;
        transition: all 0.5s ease;

        &:hover {
          transform: translateY(5px);
        }

        a {
          transition: all 0.5s ease;
          color: rgb(218, 218, 218);
          text-decoration: none;
          cursor: pointer;

          &:hover {
            color: white;
          }
        }
      }
    }
  }

  // Mobile menu
  &__mobile_menu {
    cursor: pointer;

    span {
      display: block;
      height: 3px;
      width: 25px;
      margin: 0.25rem;
      background-color: white;
    }
  }
}

// Media Queries
@media screen and (min-width: 765px) {
  .header {
    margin: 2rem;
    &__desktop_nav {
      display: block;
    }

    &__mobile_menu {
      display: none;
    }
  }
}

@media screen and (min-width: 1025px) {
  .header {
    margin: 2rem 4rem;
  }
}

@media screen and (min-width: 1750px) {
  .header {
    margin: 2rem 20rem;
  }
}
